body {
  background: #f1f1f1;
  padding:50px;
}

div #crypto-container {
  background:white;
  width: 40%;
  margin: 0 auto 8px auto;
  padding: 2em;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

div #average-container {
  background:white;
  width: 40%;
  margin: 0 auto 8px auto;
  padding: 5em;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.flex-container-blue {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  background-color: #303F9F;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.flex-container-blue > div {
  background-color: #f1f1f1;
  margin: 25px;
  padding: 25px;
  font-size: 20px;
}

.flex-container-red {
  display: -ms-flexbox;
  display: flex;
  /*background-color: #FF5252;*/
}

.flex-container-red > div {
  background-color: #f1f1f1;
  margin: 10px;
  padding: 5px;
  font-size: 15px;
}

span.left {
  padding-right: 250px;
  font-weight: bold;
}

span.right {
  float: right;
}

.maintxt{
  color: #3F51B5;
  text-decoration: underline;
}

h1{ font-size: 250%;
  font-family: 'Fira Sans', sans-serif;
}

h1.title{
  text-align: center;
  color: #FF5252;
  text-shadow: 1px 1px 0 lightgrey;
}

h1.title.crypto{
  text-align: left;
}

h1.title.main{
  text-align: left;
  font-size: 400%;
  color: #303F9F;
}

h2{ font-size: 175%;
  font-family: 'Fira Sans', sans-serif;
}

h2.label{
  color: #3F51B5;
  text-align: left;

}

h3{ font-size: 170%;
  font-family: 'Fira Sans', sans-serif;
}

h4{ font-size: 120%;
  font-family: 'PT Serif', serif;

}

h5{ font-size: 110%;
  font-family: 'PT Serif', serif;

}

h6{ font-size: 95%;
  font-family: 'PT Serif', serif;

}

table {
  padding: 15px;
}

table.newsfeed{
  width: 50%;
}

/* unvisited link */
a:link {
  text-decoration: none;
  color: #FF5252;
  font-family: 'Inconsolata', monospace;
}

/* visited link */
a:visited {
  color: #FF5252;
}

/* mouse over link */
a:hover {
  color: #303F9F;
}

/* selected link */
a:active {
  color: blue;
}

.desc{
  padding: 1em;
  text-align: center;
}

.cryptobox{
  background-color: #C5CAE9;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.sectxt{
color: #757575;
}

.sectxt.legend{
  background-color: #f1f1f1;
  font-size: 105%;
  font-style: oblique;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  margin-bottom: 5px;
}

.legendtxt{
  background-color: #3F51B;
  font-size: 125%;
  font-style: normal;
}

.indextxt{
  color: #757575;
}

.pricetxt{
  color: #3F51B5;
}

.currencies{
  width: 180px;

}

th.tabhead{
  text-align: left;
  vertical-align: top;
  color: #757575;
}

td.currencies{
  padding-right: 250px;
}

td.averages{
  text-align: Right;
  vertical-align: top;
  color: #3F51B5;
  font-family: 'Inconsolata', monospace;
}

p.articletext{
  color: #757575;
  font-family: 'PT Serif', serif;

}




